@import url(https://fonts.googleapis.com/css?family=Roboto:300i,400,900&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


* {
    padding: 0;
    margin: 0;
}

body {
    background: #274684;
}

.app {
  max-width: 500px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  border: none;
  border-radius: 5px;
  background: white;
  padding: 50px;
  margin: auto;
  margin-top: 50px;
}

.header1 {
    font-size: 45px;
    margin-bottom: 20px;
}

.span-h1 {
    font-size: 30px;
    font-weight: 300;
    font-style: italic;
    color: #709fdc;
}

.btn {
    font-size: 24px;
    color: #fff;
    background-color: #274684;
    border-radius: 5px;
    padding: 5px 10px;
    margin: 8px;
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-transition: .5s;
    transition: .5s;
}

.btn:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.tasks-list {
    margin-top: 20px;
    color: #274684;
}

.task {
    list-style: none;
    font-size: 20px;
    padding: 2px;
}

.span-number {
    color: #d50000;
    font-weight: 700;
}

